.item {
  display: flex;
  flex-direction: column;
  border: 1px solid #000;
  border-radius: 7px;
  min-width: 250px;
}

.item > div {
  padding: 5px;
}

.title {
  border-bottom: 1px solid #000;
  font-weight: bold;
}

.content {
  display: flex;
  flex-direction: column;
  row-gap: 5px;
}

.content a {
  text-decoration: none;
  border-radius: 4px;
}

.content a.active div {
  box-shadow: 0px 2px 0 0px forestgreen;
}

.footer {
  display: flex;
  font-size: 16px;
  color: grey;
  cursor: pointer;
}

.newTaskText {
  font-family: Arial, Helvetica, sans-serif;
  resize: none;
  font-size: 14px;
}